* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: #000000;
  color: #ebeae8;
  font-size: 15px;
  line-height: 1.5;
  /* overflow: 0; */
}

body::-webkit-scrollbar {
  width: 0.5rem; /* Set a small width for the scrollbar (adjust as needed) */
  background: transparent;
}

a {
  color: #ebeae8;
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
  width: 95%;
  max-width: 1400px;
  margin: auto;
}

/* Nav */
.main-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 60px;
  padding: 20px 0;
  font-size: 13px;
}

.main-nav .logo {
  width: 35px;
  margin-left: 20px;
  /* max-height: 100%; */
}

.main-nav ul {
  display: flex;
  justify-content: flex-end;
}

.main-nav ul li {
  padding: 0 10px;
}

.main-nav ul li a {
  padding-bottom: 2px;
}

.main-nav ul li a:hover {
  border-bottom: 2px solid #ff7878;
}

.main-nav ul.main-menu {
  flex: 1;
  margin-left: 20px;
  font-size: initial;
}
.main-menu li:nth-child(1) a {
  color: red;
}


.menu-btn {
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 30px;
  z-index: 2;
  display: none;
}

.btn {
  cursor: pointer;
  display: inline-block;
  border: 0;
  font-weight: bold;
  padding: 10px 20px;
  background: #2d2d2d;
  color: #ebeae8;
  font-size: 15px;
  ;
}

.btn:hover {
  opacity: 0.9;
}

.dark {
  color: #fff;
}

.dark .btn {
  background: #f4f4f4;
  color: #333;
}

/* Showcase */
.showcase {
  width: 100%;
  height: 97vh;
  background: url('../images/herobg1.png') no-repeat center center/cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: flex-end;
  padding-bottom: 50px;
  margin-bottom: 20px;
}


.showcase p {
  margin-bottom: 10px;
}

.showcase .btn {
  margin-top: 20px;
}

/* Home cards */
.home-cards {
  display: flex;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
  margin-bottom: 40px;
}

.home-cards img {
  width: 100%;
  margin-bottom: 20px;
  border-radius: 10px;
}

.home-cards h3 {
  margin-bottom: 5px;
}

.home-cards a {
  display: inline-block;
  padding-top: 10px;
  color: #0067b8;
  text-transform: uppercase;
  font-weight: bold;
}

.home-cards a:hover i {
  margin-left: 10px;
}

.sketchfab-embed-wrapper {
  display: flex;
  justify-content: center;
  flex-flow: column-reverse;
}

/* Xbox */
.xbox {
  width: 100%;
  height: 80vh;
  background-image: url("/images/premium-wallpaper.jpg");
  background-size: cover;
  background-position: center;
  /* background-repeat: no-repeat;
  height: 500px; */
  margin-bottom: 20px;

  
}

.xbox .content {
  width: 50%;
  padding: 25px 0 0 25px;
}

.xbox p,
.carbon p {
  margin: 10px 0 20px;
}

/* Carbon */
.carbon {
  width: 100%;
  height: 350px;
  background: url('https://getwallpapers.com/wallpaper/full/2/0/1/1406203-free-car-black-background-2560x1600.jpg') no-repeat center center/cover;
}

.carbon .content {
  padding: 15px 0 0 30px;
}

/* Follow */
.follow {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin: 30px 0 30px;
}

.follow * {
  margin-right: 10px;
}

/* Links */
.links {
  background: rgb(17 8 17);
  color: rgb(102 77 77);
  font-size: 12px;
  padding: 35px 0px;
}

.links-inner {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  /* grid-template-columns: repeat(6, 1fr); */
  /* grid-gap: 10px; */
  justify-content: space-evenly;
}

.links li {
  line-height: 2.8;
}

/* Footer */
.footer {
  background: rgb(0 0 0);
  color: rgb(97, 97, 97);
  font-size: 15px;
}

.footer-inner {
  width: 95%;
  max-width: 1400px;
  margin: auto;
  justify-content: space-between;
}

.footer div {
  /* margin-bottom: 16px; */
  display: flex;
  align-items: center;
}

.footer div i {
  margin-right: 10px;
}

.footer ul {
  display: flex;
  flex-wrap: wrap;
}

.footer li {
  margin-right: 30px;
  margin-bottom: 20px;
}

@media(max-width: 700px) {
  .menu-btn {
    display: block;
  }

  .menu-btn:hover {
    opacity: 0.5;
  }



  .main-nav ul.main-menu {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    background: #f2f2f2;
    width: 50%;
    height: 100%;
    border-right: #ccc 1px solid;
    opacity: 0.9;
    padding: 30px;
    transform: translateX(-500px);
    transition: transform 0.5s ease-in-out;
  }

  .main-nav ul.main-menu li {
    padding: 10px;
    border-bottom: #ccc solid 1px;
    font-size: 15px;
  }

  .main-nav ul.main-menu li:last-child {
    border-bottom: 0;
  }

  .main-nav ul.main-menu.show {
    transform: translateX(-20px);
  }

  .home-cards {
    grid-template-columns: repeat(2, 1fr);
  }

  .xbox .content p {
    display: none;
  }

  .xbox .content h2 {
    margin-bottom: 20px;
  }

  .carbon .content {
    width: 85%;
  }

  .links .links-inner {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media(max-width: 500px) {
  .home-cards {
    grid-template-columns: 1fr;
  }

  .links .links-inner {
    grid-template-columns: 1fr;
  }

  .links .links-inner ul {
    margin-bottom: 20px;
  }
}

.card {
  padding-bottom: 25px;
  background-color: #030303;
  /* padding: 20px; */
  border-radius: 10px;
  box-shadow: 1px 5px 9px rgb(140 94 228 / 16%);
  flex: 1;
  /* margin-right: 50px; */
  height: 100%;
  /* min-width: 21%; */
  min-height: 311px;
}



